export const transformFetchError = (error: string): string => {
    const errorMap: Record<string, string> = {
        already_exists: "     .",
        invalid_login_or_pass: " email  .",
        not_found_error: " email.",
    };

    if (errorMap[error]) {
        return errorMap[error];
    } else {
        return "";
    }
};
